.tree-view .MuiTreeItem-root {
  position: relative;
  padding-left: 0.1rem;
}

/* Set the font size for span elements within the tree */
.tree-view .MuiTypography-root,
.tree-view .MuiFormControlLabel-label,
.tree-view .MuiTreeItem-label {
  font-size: 0.8rem; /* Adjust as needed */
}

/* Vertical line for the tree */
.tree-view .MuiTreeItem-group {
  padding-left: 1rem;
  position: relative;
}

.MuiTreeItem-content {
  height: 2rem;
}

.MuiTypography-root.MuiTreeItem-label.MuiTypography-body1 {
  height: 2rem;
}

/* Horizontal line connecting to the parent */
.tree-view .MuiTreeItem-content:not(.no-before):before {
  content: "";
  position: absolute;
  left: 0rem;
  top: 0.85rem;
  height: 0.9rem; /* Adjust as needed */
  width: 1.5rem; /* Adjust to match the left margin of .MuiTreeItem-group */
  transform: translateY(-50%);
}

/* Ensure the label spans are displayed correctly */
.tree-view .MuiFormControlLabel-root {
  display: flex;
  align-items: center;
  justify-content: flex-start; /* Aligns text to the left */
  text-align: left; /* Ensures text within spans is left-aligned */
  width: fit-content; /* Ensures the flex container takes full width */
  position: relative;
  top: -0.1rem;
  white-space: nowrap;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.MuiFormControlLabel-label.Mui-disabled {
  color: #ff0000 !important; /* Bright Red */
  text-shadow: 0 0 5px rgba(255, 0, 0, 0.7), 0 0 10px rgba(255, 0, 0, 0.5); /* Glowing effect */
  animation: blink 1s infinite; /* Blinking animation */
}

#disabled_div_enable {
  pointer-events: none; /* Disable all interactions */
  opacity: 0.5; /* Dim the div to indicate it's disabled */
}

span > div > svg.MuiSvgIcon-root {
  scale: 0.8;
}

/* Vertical scrolling */
.tree-view {
  scroll-behavior: smooth;
  background: rgb(248 249 251);
  overflow: auto;
}

.y_scroll_enable {
  overflow-y: scroll;
  scrollbar-width: auto;
  max-height: 100%;
}

.x_scroll_enable {
  overflow-x: scroll;
  scrollbar-width: auto;
  max-width: 100%;
}

.y_scroll_disable {
  overflow-y: hidden;
}

.x_scroll_disable {
  overflow-x: hidden;
}

.MuiCheckbox-root {
  color: var(
    --checkbox-color
  ) !important; /* Dynamically set the checkbox color based on the current theme */
}

.MuiCheckbox-colorSecondary.Mui-checked {
  color: var(
    --primary-color
  ) !important; /* Use primary color for checked state */
}

/* Default (Light Theme) */
.tree-view {
  background: rgba(248, 249, 251, 0.1);
  border: 1px solid rgba(49, 51, 63, 0.2) !important;
  border-radius: 0.5rem;
  padding: calc(-1px + 1rem);
}

/* Space above the first content */
li#\30 {
  top: 0.6em;
}

/* Style the scrollbar */
.tree-view::-webkit-scrollbar {
  background: rgba(248, 249, 251, 0.1);
  width: 12px; /* Scrollbar width */
  height: 12px; /* Scrollbar height */
}

/* Scrollbar track */
.tree-view::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px; /* Rounded corners for track */
}

/* Scrollbar handle */
.tree-view::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px; /* Rounded corners for handle */
}

/* Scrollbar handle on hover */
.tree-view::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.tree-view::-webkit-scrollbar-corner {
  background: transparent;
}
